body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #DEE4FF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo-btn{
    height: 120px;
    width: 120px;
}
.nav-btn{
    display: flex;
    flex-direction: row;
    background-color: white;
    margin:10px;
   
}
.head-btn{
    color: #DAA520;
    font-weight: bold;
    padding-top: 20px;
}
.text-bot{
    font-size: medium;
    font-weight: 100;
}

  
  .main {
    margin-top: 5em;
  }
  
  .center {
    justify-content: center;
    padding: 10px;
  }
  
  .ui.search input {
    width: 100%;
    border-radius: 0 !important;
  }
  
  .item {
    padding: 15px 0px !important;
    border-style:hidden hidden solid hidden;
    border-radius: 5px;
    border-width: 3px;
    border-color: darkgray;
    margin-left:5px ;
    margin-right: 5px;
    

  }
  
  i.icon {
    float: right;
    font-size: 20px;
    cursor: pointer;
  }
  

  /* Parctice section */
  .pract1{
    display: flex;
    flex-direction: row;
    text-align: center;
    margin: 0;
  }
  .pract2{
    position:absolute;
    padding: 10px;
    border:2px solid rgb(9, 107, 43);
    background-color:white;
    color:green;
    border-radius: 50px;
  
    color:black;
    cursor: pointer;
  }
  .pract2:hover{
    background-color: rgb(79, 236, 103);
  }
  
  .list{
    border:1px solid black;
    border-radius: 10px;
    padding:30px;
  }
  .top{
    padding-left: 70px;
  }
  .jithu{
    margin:30px;
    margin-top: 0px;
    background-color: rgb(254, 229, 226);
  }
